@font-face {
  font-family: 'Metropolis';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: local('Metropolis Regular'), local('Metropolis-Regular'),
  url('#{$font-path}Metropolis-Regular.woff2') format('woff2'), url('#{$font-path}Metropolis-Regular.woff') format('woff') {} }

@font-face {
  font-family: 'Metropolis';
  font-style: italic;
  font-display: swap;
  font-weight: 400;
  src: local('Metropolis Italic'), local('Metropolis-Italic'),
  url('#{$font-path}Metropolis-RegularItalic.woff2') format('woff2'), url('#{$font-path}Metropolis-RegularItalic.woff') format('woff') {} }

